@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.app{
    max-width: 1200px;
    margin: 20px auto
}
a{
    text-decoration: none;
    color:#000;
    color:initial;
}
a, p, div{
    font-family: 'Roboto', sans-serif;
}
.header__logo{
  cursor: pointer;
  -webkit-animation: left-slide 1s ease-in-out;
          animation: left-slide 1s ease-in-out;
}
.header{
  display: flex;
  justify-content: space-between;
  position: fixed;
  max-width: 1200px;
  width: 100%;
}
.menu{
  display: flex;
  list-style-type: none;
  -webkit-animation: right-slide 1s ease-in-out;
          animation: right-slide 1s ease-in-out;
}
.menu__element{
margin-left:20px ;
}

.menu__element__link{
  font-size:22px;
}
@-webkit-keyframes  right-slide {
  0% {
    transform: translateX(200px) rotate(0);
  }
  80% {
    transform: translateX(-20px) rotate(-10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@keyframes  right-slide {
  0% {
    transform: translateX(200px) rotate(0);
  }
  80% {
    transform: translateX(-20px) rotate(-10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@-webkit-keyframes left-slide {
  0% {
    transform: translateX(-200px) rotate(0);
  }
  80% {
    transform: translateX(20px) rotate(10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@keyframes left-slide {
  0% {
    transform: translateX(-200px) rotate(0);
  }
  80% {
    transform: translateX(20px) rotate(10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
